/**
 * axios,axios,axios
 * then,(axios{config,data,header,request},data),
 * retCode0000,catch(reject),,catch,catch(()=>{})
 * ,intercept
 * @returns axios interceptor instance
 */
import Axios from 'axios'
import util from './util'
import store from '../store/index'
// 
const instance = Axios.create({
  baseURL: util.apiServer,
  url: '',
  timeout: 20000,
  responseType: 'json',
  withCredentials: true,
  intercept: 'all' // ,all:thencatch,resolve:then,reject:catch,none:thencatch
})
instance.interceptors.request.use((config) => {
  store.commit('toggleLoading');
  return config
})
instance.interceptors.response.use(
  (res) => {
    if (
      res.config.intercept !== 'none' &&
        res.config.intercept !== 'reject'
    ) {
      if (res.data.retCode === '0000') {
        // return res
      }
    }
    store.commit('toggleLoading');
    return res
  },
  (err) => {
    store.commit('toggleLoading');
    return Promise.reject(err)
  }
)

export default instance



