<template>  
  <div class="home" v-show="show">
    <div class="logo"></div>
    <div class="discount">
      <div class="item" v-for="item in discount" :key="item.id" v-show="isShow(item.id)" @click="go(item.linkName)">
        <img :src="item.imgPath" alt="">
        <p>{{item.text}}</p>
      </div>
    </div>
    <p class="title"></p>
    <div class="mySubscibe">
      <div class="item" v-for="item in mySubscibe" :key="item.id" v-show="isShow(item.id)" @click="go(item.linkName)">
        <img :src="item.imgPath" alt="">
        <p>{{item.text}}</p>
      </div>
    </div>
    <p class="title"></p>
    <div class="fnModule">
      <div class="item" v-for="item in fnModule" :key="item.id" @click="scan">
        <img :src="item.imgPath" alt="">
        <p>{{item.text}}</p>
      </div>
    </div>
  </div>
</template>  
  
<script>
import util from "@/pages/index/helper/util";
import wx from "weixin-js-sdk";
import scanAPI from "@/pages/index/services/scan";
import homeApi from "@/pages/index/services/home";
export default {
  data() {
    return {
      show: false,
      shouldShow: [],
      discount: [
        {
          imgPath: require("../assets/home/discount.png"),
          text: "",
          linkName: "discountList",
          id: 1
        },
        {
          imgPath: require("../assets/home/creatediscount.png"),
          text: " ",
          linkName: "createDiscount",
          id: 2
        }
      ],
      mySubscibe: [
        {
          imgPath: require("../assets/home/a.png"),
          text: "",
          linkName: "",
          id: 3
        },
        {
          imgPath: require("../assets/home/b.png"),
          text: "",
          linkName: "chartPeriodComparison",
          id: 4
        },
        {
          imgPath: require("../assets/home/c.png"),
          text: "",
          linkName: "",
          id: 5
        },
        {
          imgPath: require("../assets/home/d.png"),
          text: "",
          linkName: "chartCompanyMarket",
          id: 6
        },
        {
          imgPath: require("../assets/home/e.png"),
          text: "",
          linkName: "chartCommodityMarket",
          id: 7
        }
      ],
      fnModule: [
        {
          imgPath: require("../assets/home/scan.png"),
          text: "",
          id: 8
        }
      ]
    };
  },
  methods: {
    check() {
      const company_id = util.getRequest().company_id
        ? util.getRequest().company_id
        : window.localStorage.getItem("company_id");
      const wx_userid = util.getCookie("wx_userid");
      util.setStorage("company_id", company_id);
      if (wx_userid) {
        this.show = true;
        const params = {
          company_id: company_id,
          wx_user_id: wx_userid
        };
        homeApi.menus({ params: params }).then(res => {
          const { data } = res.data;
          this.shouldShow = data;
        });
      } else {
        homeApi.check({ params: { company_id: company_id } }).then(res => {
          const { status, uri, message } = res.data;
          if (status == "0") {
            window.location.href = uri;
          } else {
            this.$toast(message);
          }
        });
      }
    },
    isShow(id) {
      return this.shouldShow.includes(id);
    },
    go(urlName) {
      if (urlName) {
        this.$router.push({
          name: urlName
        });
      }
    },
    scan: function() {
      this.$router.push({
        name: "scan"
      });
      // const params = {
      //   url: window.location.href,
      //   company_id: window.localStorage.getItem("company_id")
      // };
      // const _this = this;
      // scanAPI.wxSdkConfig({ data: params }).then(res => {
      //   const { data } = res.data;
      //   wx.config({
      //     beta: true, // wx.invokejsapi
      //     // ,apialertpclogpc
      //     debug: true,
      //     // 
      //     appId: res.data.appid,
      //     // 
      //     timestamp: data.timestamp,
      //     // 
      //     nonceStr: data.nonceStr,
      //     // 
      //     signature: data.signature,
      //     // JSJS
      //     jsApiList: ["scanQRCode"]
      //   });
      // });

      // wx.ready(function() {
      //   wx.scanQRCode({
      //     desc: "scanQRCode desc",
      //     needResult: 1, // 01
      //     scanType: ["qrCode"], // 
      //     success: function(res) {
      //       var result = res.resultStr; // needResult  1 
      //       var a = result.split("/");
      //       _this.$router.push({
      //         name: "scanResult",
      //         params: { code: a[a.length - 1] }
      //       });
      //     }
      //   });
      // });
    }
  },
  mounted() {
    this.check();
  }
};
</script>  
  
<style lang="scss" scoped>
.home {
  background: #e5e5e5;
  .logo {
    width: 750px;
    height: 200px;
    background: rgba(81, 184, 203, 1);
    background-image: url("../assets/home/logo.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 120px 120px;
    text-align: center;
    line-height: 200px;
    font-size: 34px;
    color: rgba(255, 255, 255, 1);
  }
  .discount {
    width: 750px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background: #ffffff;
    .item {
      width: 50%;
      height: 250px;
      box-sizing: border-box;
      text-align: center;
      font-size: 26px;
      color: rgba(114, 113, 113, 1);
      border-bottom: 1px solid #e5e5e5;
      display: flex;
      flex-direction: column;
      justify-content: center;
      img {
        width: 104px;
        margin: 6px auto;
      }
      &:nth-child(odd) {
        border-right: 1px solid #e5e5e5;
      }
    }
  }
  .title {
    font-size: 30px;
    padding: 20px;
    color: rgba(152, 152, 152, 1);
  }
  .mySubscibe,
  .fnModule {
    width: 750px;
    display: flex;
    flex-wrap: wrap;
    background: #ffffff;
    .item {
      width: 250px;
      height: 250px;
      font-size: 26px;
      color: rgba(152, 152, 152, 1);
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
      box-sizing: border-box;
      border-right: 1px solid #e5e5e5;
      border-bottom: 1px solid #e5e5e5;
      img {
        width: 60px;
        height: 60px;
        margin: 6px auto;
      }
    }
  }
}
</style>  


